<template>
  <div class="flex flex-row gap-2">
    <img class="w-12 h-12 rounded-full" v-if="!!imageUrl" :src="imageUrl" :alt="imageAlt || text" />
    <div class="w-12 h-12 rounded-full pt-3 pb-3 bg-blue-500 text-center text-white font-bold" v-else>
      <span>{{ abbr }}</span>
    </div>
    <div class="pt-3 pb-3 font-bold">{{ text }}</div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
export default defineComponent({
  props: {
    imageUrl: String,
    imageAlt: String,
    text: String,
  },
  computed: {
    abbr(): string {
      return (
        this.text
          ?.split(" ")
          .map((s) => s[0])
          .join("") ?? ""
      );
    },
  },
});
</script>
