
import { defineComponent } from "vue";
export default defineComponent({
  props: {
    imageUrl: String,
    imageAlt: String,
    text: String,
  },
  computed: {
    abbr(): string {
      return (
        this.text
          ?.split(" ")
          .map((s) => s[0])
          .join("") ?? ""
      );
    },
  },
});
