    class="flex flex-row gap-2 outline-none text-white font-bold py-2 px-4"
    v-bind:class="{
      rounded: variant === 'rounded',
      'rounded-full': variant === undefined || variant === 'pill',
    }"
  >
    <i v-if="icon" :class="`ms-Icon ms-Icon--${icon} leading-6`"></i>
    <slot v-if="!text"></slot>
    <span v-else class="leading-6">{{ text }}</span>
  </button>
</template>
<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    icon: String,
    variant: Boolean,
    text: String,
  },
});
</script>
